@import "https://fonts.googleapis.com/css2?family=Special+Elite&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  background: #fff;
  font-family: Special Elite, Arial, Helvetica, sans-serif;
  overflow: hidden;
}

ul {
  list-style: none;
}

.header {
  height: 20vh;
  text-align: center;
  margin-top: 10vh;
}

.header h1 {
  color: #2c2c2c;
  text-shadow: 1px 1px 5px #eee;
  font-size: 40px;
}

.card-list {
  text-align: center;
  flex-flow: wrap;
  justify-content: space-evenly;
  margin-top: 40px;
  display: flex;
}

.card-item {
  width: 350px;
  height: 350px;
  cursor: pointer;
  border-radius: 20px;
  margin-bottom: 50px;
  transition: all .5s;
  box-shadow: 1px 1px 5px #333;
}

.card-link {
  width: 100%;
  height: 100%;
  color: #2c2c2c;
  justify-content: center;
  align-items: end;
  margin-bottom: 10px;
  padding-bottom: 10px;
  font-size: 22px;
  text-decoration: none;
  display: flex;
}

.passiveimage {
  z-index: -2;
  filter: grayscale() blur(3px);
  -webkit-filter: grayscale() blur(3px);
  -moz-filter: grayscale(100%) blur(3px);
  -o-filter: grayscale(100%) blur(3px);
  -ms-filter: grayscale(100%) blur(3px);
  transform: scale(.95);
}

.d-none {
  display: none;
}

.card-item:hover {
  transform: scale(1.3);
  box-shadow: 3px 3px 15px #333;
}

.task-image-1 {
  background: url("image-task-1.d05ce688.png") center / 300px 400px;
}

.task-image-2 {
  background: url("image-task-2.5241e9a9.png") center / cover;
}

.task-image-3 {
  background: url("image-task-3.f7bc8272.png") center / cover;
}

